.about {
  width: 100%;
  min-height: 80vh;
  position: relative;
  z-index: 1;
  display: flex;
  overflow: hidden;
  background: url("./../img/footer_background.jpg") no-repeat center center
    fixed;
  background-size: cover;
  @include mobile() {
    height: auto;
    flex-wrap: wrap;
    padding-bottom: 10px;
  }

  &__list {
    list-style: none;

    &-item {
      text-align: left;
      font-size: 1.4rem;
      // font-weight: bold;
      line-height: 2rem;
      margin-left: 16px;

      &::before {
        content: "\2022";
        color: white;
        // font-weight: 300;
        display: inline-block;
        width: 1em;
        margin-left: -1em;
      }
    }
  }

  .about-box-1 {
    margin-left: 5%;
    flex-basis: 35%;
    background: black;
    z-index: 1;
    padding: 20px;
    text-align: justify;
    color: white;
    font-size: 1.4rem;
    line-height: 1.8rem;
    opacity: 0.8;

    p {
      margin: 3vh 0 3vh 0;
    }

    @include mobile() {
      width: 100%;
      flex-basis: auto;
      margin-left: 0;
      p {
        margin: 0;
      }
    }
  }

  .about-box-2 {
    flex-basis: 65%;
    display: flex;
    z-index: 1;
    justify-content: center;
    align-items: center;
    overflow: hidden;

    @include mobile() {
      width: 100%;
      flex-basis: auto;
      justify-content: center;
      align-items: normal;
    }
    .about-bullet {
      width: 70%;
      height: 70%;
      display: flex;
      @include mobile() {
        flex-wrap: wrap;
      }

      .about-txt-2 {
        flex-basis: 50%;
        flex-wrap: wrap;
        @include mobile() {
          flex-basis: 100%;
        }
      }

      .about-txt-1 {
        flex-basis: 50%;
        flex-wrap: wrap;
        @include mobile() {
          flex-basis: 100%;
        }
        .atut {
          flex-basis: 100%;
          margin-top: 20px;
          display: flex;
          transition: 0.9s;
          transform: translate(300%, 0);

          .atut-img {
            flex-basis: 30%;

            img {
              width: 80%;
            }
          }

          .atut-span {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-basis: 69%;
            text-align: center;
            font-weight: 600;
            font-size: 1.6rem;
          }
        }

        .showUp {
          transform: translate(0, 0);
        }
      }
    }
  }

  .listener {
    background: transparent;
    z-index: 999;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 150vh;
    z-index: 999;
    @include mobile() {
      display: none;
    }
  }

  .about-img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    transform: scale(1.1);
    left: 0;
    top: 0;
    background-image: url("../img/19366.jpg");
    background-size: cover;
  }
}
